<template>
    <div class="inner_page">
        <menuicon></menuicon>
        <loader :show="Loading"></loader>
        <div class="container" v-show="!Loading">
            <div class="row inner_project">
                <div class="col-md-5 col-sm-5 col-xs-12">
                    <div class="project_cover">
                        <a :href="project.header" data-lightbox="gallery" :data-title="project.title">
                            <img :src="project.header" alt="" class="img-responsive">
                        </a>
                    </div>
                    <div class="row">                        
                        <div class="col-md-6 col-sm-12 col-xs-12 grid-col" v-for="(item, index) in project.gallery">
                            <a :href="item.src" class="grid-item" data-lightbox="gallery" :data-title="project.title">
                                <img :src="item.src">
                            </a>
                        </div>
                    </div>
                </div>
                <div class="col-md-5 col-sm-5 col-xs-12">
                    <div class="inner_project_header">
                        <h2>{{project.title}}</h2>
                        <h3>{{project.description}}</h3>
                    </div>
                    <div class="inner_project_description">
                        <div class="dynamic_text" v-html="project.content"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MenuIcon from '../components/menuicon'
import Loader from '../components/loader'

export default {
    name: 'project',
    data () {
        return {
            id:'',
            project:{},
            Loading:false 
        }
    },
    components: {
        'menuicon': MenuIcon,
        'loader':Loader
    },
    created() {
        return this.GetData(),
        document.title = "Project Detail";
    },
    methods : {
        GetData(){
            this.Loading = true;
            this.$http.get('http://tarhoasar.hex.team/api/getwidget/project')
            .then(function(res) {
                console.log(res.data);
                var item = res.data;
                for(var i = 0;item.length > i; i++){
                    if(item[i].id == this.$route.params.id){
                        this.project = JSON.parse(item[i].data);
                        this.project.content = this.project.content.replace(/\r?\n/g, '<br />');
                        this.id = this.$route.params.id;
                    }
                }
                this.Loading = false;
            },function(err){
                console.log(err);
                this.Loading = false;
            });
        }
    }
}
</script>

<style>
.inner_project_header {
    text-align: left;
    position: relative;
    padding-left: 20px;
}

.inner_project_header:before {
    content: '';
    position: absolute;
    left: 0;
    width: 5px;
    height: 100%;
    top: 0;
    bottom: 0;
    background: #EDED43;
}

.inner_project_header h2 {
    font-size: 26px;
    color: #666666;
    font-family: "IRANSANS BOLD";
    margin: 0;
    margin-bottom: 10px;
    line-height: normal;
    text-align: left;
}

.inner_project_header h3 {
    font-size: 21px;
    color: #999999;
    margin: 0;
    text-align: left;
}

.inner_project_description {
    font-size: 14px;
    color: #666;
    margin: 20px auto;
    text-align: left;
}

.inner_project_description span {
    display: block;
    margin-bottom: 15px;
}

.inner_project_content p {
    font-size: 14px;
    color: #999999;
    text-align: justify;
    direction: ltr;
}

.inner_project .project_cover {
    position: relative;
    cursor: pointer;
    margin-bottom: 25px;
}

.inner_project .project_cover a {
    display: grid;
    position: relative;
}

.inner_project .project_cover a:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 25px;
    height: 25px;
    opacity: 0;
    background-image: url(data:image/svg+xml;utf8;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    -ms-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: opacity .2s ease-out;
    -moz-transition: opacity .2s ease-out;
    -ms-transition: opacity .2s ease-out;
    -o-transition: opacity .2s ease-out;
    transition: opacity .2s ease-out;
}

.inner_project .project_cover a:hover:after {
    opacity: 1;
    -webkit-transition: opacity .2s ease-out;
    -moz-transition: opacity .2s ease-out;
    -ms-transition: opacity .2s ease-out;
    -o-transition: opacity .2s ease-out;
    transition: opacity .2s ease-out;
    -webkit-transition-delay: .25s;
    -moz-transition-delay: .25s;
    -o-transition-delay: .25s;
    transition-delay: .25s;
}

.inner_project .project_cover a:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -ms-transform: scale(0, 0);
    -moz-transform: scale(0, 0);
    -o-transform: scale(0, 0);
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
    -webkit-transition: transform .2s ease-out;
    -moz-transition: transform .2s ease-out;
    -ms-transition: transform .2s ease-out;
    -o-transition: transform .2s ease-out;
    transition: transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}

.inner_project .project_cover a:hover:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -ms-transform: scale(1, 1);
    -moz-transform: scale(1, 1);
    -o-transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transition: transform .2s ease-out;
    -moz-transition: transform .2s ease-out;
    -ms-transition: transform .2s ease-out;
    -o-transition: transform .2s ease-out;
    transition: transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}

.inner_project .project_cover img {
    max-height: 400px;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.project_gallery {
    margin: 10px auto;
}

.project_gallery:after {
    content: '';
    display: block;
    clear: both;
}

.grid-col {
    margin-bottom: 30px;
}

.grid-item {
    cursor: pointer;
    position: relative;
    display: block;
}

.grid-item img {
    display: block;
    width: 100%;
    max-height: 300px;
    min-height: 300px;
    object-fit: cover;
    object-position: center;
}

.grid-item:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 25px;
    height: 25px;
    opacity: 0;
    background-image: url(data:image/svg+xml;utf8;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    -ms-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: opacity .2s ease-out;
    -moz-transition: opacity .2s ease-out;
    -ms-transition: opacity .2s ease-out;
    -o-transition: opacity .2s ease-out;
    transition: opacity .2s ease-out;
}

.grid-item:hover:after {
    opacity: 1;
    -webkit-transition: opacity .2s ease-out;
    -moz-transition: opacity .2s ease-out;
    -ms-transition: opacity .2s ease-out;
    -o-transition: opacity .2s ease-out;
    transition: opacity .2s ease-out;
    -webkit-transition-delay: .25s;
    -moz-transition-delay: .25s;
    -o-transition-delay: .25s;
    transition-delay: .25s;
}

.grid-item:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -ms-transform: scale(0, 0);
    -moz-transform: scale(0, 0);
    -o-transform: scale(0, 0);
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
    -webkit-transition: transform .2s ease-out;
    -moz-transition: transform .2s ease-out;
    -ms-transition: transform .2s ease-out;
    -o-transition: transform .2s ease-out;
    transition: transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}

.grid-item:hover:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -ms-transform: scale(1, 1);
    -moz-transform: scale(1, 1);
    -o-transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transition: transform .2s ease-out;
    -moz-transition: transform .2s ease-out;
    -ms-transition: transform .2s ease-out;
    -o-transition: transform .2s ease-out;
    transition: transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}
.dynamic_text{
    direction: ltr;
    text-align: left;
}
.dynamic_text br{
    line-height: 2.5;
}
</style>



